import { Component } from '@angular/core';

// FORMS
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CustomValidators } from 'src/app/classes/custom-validators';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {

  public loginForm: FormGroup;

  constructor(private fb: FormBuilder) {
    this.loginForm = fb.group({
      'lemail': ['', [CustomValidators.required, Validators.email]],
      'lpassword': ['', [
        CustomValidators.required,
        CustomValidators.missingLowerCases,
        CustomValidators.missingUpperCases,
        CustomValidators.missingNumbers,
        CustomValidators.missingSpecialChars,
        Validators.minLength(6)
      ]]
    });
   }

  public xd(d:any){
    console.log(d.errors);
  }

  public login(form: FormGroup){
    console.log(form);
  }

  get lemail(){
    return this.loginForm.get('lemail');
  }

  get lpassword(){
    return this.loginForm.get('lpassword');
  }

}
